	module.exports = {"news":{"heading":"In the News","data":[{"id":"1","link":"/gojek-expansion","image":"../../images/news/press-release.jpg","name":"GO-JEK announces the launch of locally founded companies in Vietnam and Thailand","date":"JUNE 25, 2018","text":"Bangalore, India– GO-JEK today announces the launch of two locally founded companies in Vietnam and Thailand that will form the first wave of its international expansion. The companies will be run by local founding management teams, with GO-JEK providing knowledge and expertise as well as technology and investment.","mainBanner":true},{"id":"2","link":"https://tech.economictimes.indiatimes.com/news/people/go-jek-appoints-ranjan-sakalley-as-cto-of-go-pay/64838395","image":"../../images/news/rajan-sakalley.jpg","name":"GO-JEK appoints Ranjan Sakalley as CTO of GO-PAY","date":"JULY 03, 2018","text":"Indonesia's ride hailing service GO-JEK has appointed Ranjan Sakalley as the chief technology officer of its payments arm GO-PAY. In this role, Sakalley will work alongside group CTO Ajey Gore to lead all product engineering for GO-PAY products including the design, delivery and reliability of the products.","mainBanner":false},{"id":"3","link":"https://www.go-jek.com/blog/go-jek-to-launch-international-expansion-into-four-new-markets/","image":"../../images/news/news-01.jpg","name":"GO-JEK to Launch International Expansion Into Four New Markets","date":"MAY 24, 2018","text":"JAKARTA, INDONESIA – GO-JEK has confirmed that it is to invest $500 million in its international expansion strategy. The company will enter Vietnam, Thailand, Singapore and the Philippines within the next few months, starting with ride-hailing but ultimately aiming to replicate the multiple-service business model that has made it the market leader in Indonesia.","mainBanner":false},{"id":"4","link":"https://www.techinasia.com/go-jek-acquisition-kartuku-mapan-midtrans","image":"../../images/news/news-02.jpg","name":"GO-JEK buys 3 fintech firms to conquer Indonesia payments","date":"DECEMBER 15, 2017","text":"Ride-hailing service GO-JEK, Indonesia's first billion-dollar tech startup, has agreed to acquire three local fintech firms as it seeks to expand its grip on the digital payments market. The deals involve Kartuku, a leading offline payments processing company in Indonesia...","mainBanner":false},{"id":"5","link":"https://www.washingtonpost.com/business/techs-global-challenge-plays-out-in-a-jakarta-school-yard/2017/12/12/ac6d8328-df8a-11e7-b2e9-8c636f076c76_story.html?noredirect=on&utm_term=.a69d0afe56eb","image":"../../images/news/news-03.jpg","name":"Tech’s Global Challenge Plays Out in a Jakarta School Yard","date":"DECEMBER 12, 2017","text":"Uber Technologies Inc. has spent millions of dollars fighting politicians and business lobbies trying to scuttle the app everywhere from New York to London. For Indonesian ride-hailing and food-delivery startup GO-JEK, the push back is coming from the lunch ladies at Jakarta Intercultural School.","mainBanner":false},{"id":"6","link":"https://www.nytimes.com/2017/12/08/technology/southeast-asia-ride-hailing.html","image":"../../images/news/news-04.jpg","name":"Southeast Asia's Ride-Hailing War is Being Waged on Motorbikes","date":"DECEMBER 8, 2017","text":"JAKARTA, Indonesia - On a recent morning driving his motorbike for one of Asia's fastest-growing tech start-ups, Nasrun picked up and dropped off four school-children, an office worker, medicine from a pharmacy, some dumplings with peanut sauce, a few documents and an order of Japanese food, the last of which he took to a woman at the Indonesia Stock Exchange","mainBanner":false},{"id":"7","link":"https://www.techinasia.com/gojek-insider-account-of-scaling-900x-doubling","image":"../../images/news/news-05.jpg","name":"An insider's account of how GO-JEK hit 900x scale in 18 months and is still doubling","date":"NOVEMBER 21, 2017","text":"GO-JEK scaled 900 times in the first 18 months after the launch of its mobile app in January 2015. By June 2016, there were over 20 million bookings on GO-JEK about 667,000 rides per day. After that, the Indonesia startup turned into a unicorn, raising a funding round of US$550 million. It did all that with a team of less than 80 engineers.","mainBanner":false},{"id":"8","link":"http://www.scmp.com/week-asia/business/article/2116287/how-indonesian-unicorn-go-jek-went-20-bikes-us25-billion-and-e","image":"../../images/news/news-04.jpg","name":"How Indonesian Unicorn GO-JEK went from 20 bikes to US $2.5 Billion & an e-money revolution","date":"OCTOBER 22, 2017","text":"Ten years ago, few could have predicted the mobile payment revolution in Indonesia would be driven not by the banking industry, but by a firm that started out as a motorcycle-taxi booking operation with just 20 riders.","mainBanner":false},{"id":"9","link":"https://yourstory.com/2017/05/techie-tuesdays-ajey-gore/","image":"../../images/news/ajay-gore.jpg","name":"How a small-town commerce graduate became CTO of a multibillion-dollar company","date":"MAY 9, 2017","text":"Ajey Gore, a commerce graduate from University of Allahabad, is a brilliant example that you don’t need a formal degree to chase your dreams. Hailing from Banda in Uttar Pradesh, today he’s the group CTO of Indonesia’s first unicorn, GO-JEK.","mainBanner":false}]}}

